@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
  display: flex;
  justify-content: center;
  align-items: center;
}

:root{--user-color: #f44336}form{background:rgba(255,255,255,0.4);display:flex;width:80%;margin:0 auto;margin-bottom:3rem;border-radius:1rem;overflow:hidden;margin-top:2rem}@media screen and (max-width: 800px){form{width:auto;margin:2rem 1rem}}form input{font-family:inherit;border:none;background:transparent;font-size:1.4rem;width:100%;padding:1rem 1.4rem}form input::-webkit-input-placeholder{opacity:0.5}form input:-ms-input-placeholder{opacity:0.5}form input::placeholder{opacity:0.5}form input:focus{outline:none}form button{width:200px;border:none;border-radius:0;background:#333;color:white;cursor:pointer;font-size:1.2rem;font-family:inherit}.user_wrapper{display:flex;flex-direction:row;padding:2rem 3rem;border-radius:1.6rem;background-color:rgba(255,255,255,0.4);align-items:center;justify-content:center;grid-gap:3rem;gap:3rem;margin-inline:2rem;margin-bottom:2rem}@media screen and (max-width: 800px){.user_wrapper{flex-direction:column;margin-inline:1rem;grid-gap:1rem;gap:1rem;padding:1rem 1.4rem}}.user_wrapper .error{text-align:center;font-size:clamp(1.2rem, 3vw, 2rem)}.user_wrapper code{background-color:rgba(255,255,255,0.8);border-radius:0.4rem;padding:0.4rem 0.6rem}.user_wrapper .col_one{display:flex;flex-direction:column;align-items:center;justify-content:center;grid-gap:1.6rem;gap:1.6rem}.user_wrapper .col_one .image_wrapper{display:inline-block;position:relative;width:250px;height:250px;background:#f44336;background:var(--user-color);box-shadow:0 0 30px 0px #f44336;box-shadow:0 0 30px 0px var(--user-color);box-sizing:border-box;-webkit-clip-path:circle(50% at center);clip-path:circle(50% at center);transition:-webkit-transform 0.4s ease-in-out;transition:transform 0.4s ease-in-out;transition:transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out}@media screen and (max-width: 800px){.user_wrapper .col_one .image_wrapper{width:200px;height:200px;margin-top:1.4rem}}.user_wrapper .col_one .profile_picture{position:absolute;top:8px;left:8px;width:234px;height:234px;-webkit-clip-path:circle(50% at center);clip-path:circle(50% at center)}@media screen and (max-width: 800px){.user_wrapper .col_one .profile_picture{top:6px;left:6px;width:188px;height:188px}}.user_wrapper .col_one .rank{color:#f44336;color:var(--user-color);font-size:1.6rem;width:80%;text-align:center;margin:0;text-transform:uppercase}.user_wrapper .user_list{list-style:none;padding:0}.user_wrapper .user_list li{font-size:clamp(1rem, 3vw, 1.4rem);margin-top:0.4rem}.user_wrapper .user_list i{width:34px;margin-right:0.4rem;text-align:center;font-size:clamp(1.2rem, 3vw, 1.6rem)}.user_wrapper .user_list .name{font-size:clamp(2rem, 5vw, 2.6rem);font-weight:bold}@media screen and (max-width: 800px){.user_wrapper .user_list .name{text-align:center}}.user_wrapper .user_list .handle{margin-bottom:1.4rem;margin-top:-0.4rem;font-size:clamp(1.2rem, 4vw, 1.6rem);font-style:italic;color:#333}@media screen and (max-width: 800px){.user_wrapper .user_list .handle{text-align:center;margin-top:0}}@media screen and (max-width: 800px){.user_wrapper .user_list{margin-top:0}}

